<template>
  <div>
    <Head title="地址列表"></Head>
    <van-address-list
      v-model="chosenAddressId"
      switchable
      :list="list"
      disabled-text="以下地址超出配送范围"
      @add="onAdd"
      @edit="onEdit"
      @select="chooseads"
    />
  </div>
</template>

<script>
import Head from "@/components/Head.vue";
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["AddressInfo","nowAds"])
  },
  data() {
    return {
      chosenAddressId: 0,
      list: []
    };
  },
  components: {
    Head
  },
  methods: {
    chooseads(item,index){
        this.chosenAddressId = index;
        this.$store.commit("chanegNowAds",item)
        // console.log(index)
        // console.log(item)
        // this.$router.go(-1);
        console.log(this.nowAds)
        this.$router.push({name:"suborder"})
    },
    onAdd() {
        this.$store.commit("changeAddressInfo", "");
      this.$router.push({ name: "changeads" });
    },

    onEdit(item, index) {
      let adsNow = item._id;
      this.$axios.get("/vue/getOneAds", { params: { adsNow } }).then(res => {
        let msg = res.data.result;
        this.$store.commit("changeAddressInfo", msg);
        this.$router.push({
          name: "changeads",
          params: {
            adsNow,
          }
        });
      });
    }
  },
  mounted() {
    const username = JSON.parse(sessionStorage.userInfo).username;
    console.log(username)
    this.$axios
      .get("/vue/getAds", {
       params:{
         username,
       }
      })
      .then(res => {
        console.log(res)
        res.data.result.forEach((element, index) => {
          let listObj = {};
          listObj._id = element._id;
          listObj.id = index;
          listObj.name = element.addressName;
          listObj.tel = element.addressTel;
          listObj.address = element.addressAds;
          this.list.push(listObj);
        });
      });
      if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
      sessionStorage.removeItem("store");
    }

    //在页面刷新时将vuex里的信息保存到sessionStorage里
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
  }
};

</script>

